	module.exports = {"data":{"markdownRemark":{"html":"<h2>Selection Sort</h2>\n<p>Selection Sort is one of the most simple sorting algorithms. It works in the following way,</p>\n<ol>\n<li>Find the smallest element. Swap it with the first element.</li>\n<li>Find the second smallest element. Swap it with the second element.</li>\n<li>Find the third smallest element. Swap it with the third element.</li>\n<li>Repeat finding the next smallest element and swapping it into the corresponding correct position till the array is sorted.</li>\n</ol>\n<p>As you can guess, this algorithm is called Selection Sort because it repeatedly selects the next smallest element and swaps it into its place.</p>\n<p>But, how would you write the code for finding the index of the second smallest value in an array? </p>\n<ul>\n<li>An easy way is to notice that the smallest value has already been swapped into index 0, so the problem reduces to finding the smallest element in the array starting at index 1.</li>\n</ul>\n<p>An implementation in C/C++ is as shown below,</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-c\"><code><span class=\"token keyword\">for</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">int</span> i <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> n<span class=\"token punctuation\">;</span> i<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">{</span>\n\t<span class=\"token keyword\">int</span> min_index <span class=\"token operator\">=</span> i<span class=\"token punctuation\">;</span>\n\t<span class=\"token keyword\">int</span> min_element <span class=\"token operator\">=</span> a<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\t\n\t<span class=\"token keyword\">for</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">int</span> j <span class=\"token operator\">=</span> i <span class=\"token operator\">+</span><span class=\"token number\">1</span><span class=\"token punctuation\">;</span> j <span class=\"token operator\">&lt;</span> n<span class=\"token punctuation\">;</span> j<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span>\n\t<span class=\"token punctuation\">{</span>\n\t\t<span class=\"token keyword\">if</span><span class=\"token punctuation\">(</span>a<span class=\"token punctuation\">[</span>j<span class=\"token punctuation\">]</span> <span class=\"token operator\">&lt;</span> min_element<span class=\"token punctuation\">)</span>\n\t\t<span class=\"token punctuation\">{</span>\n\t\t\tmin_element <span class=\"token operator\">=</span> a<span class=\"token punctuation\">[</span>j<span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\t\t\tmin_index <span class=\"token operator\">=</span> j<span class=\"token punctuation\">;</span>\n\t\t<span class=\"token punctuation\">}</span>\n\t<span class=\"token punctuation\">}</span>\n\t\n\t<span class=\"token function\">swap</span><span class=\"token punctuation\">(</span><span class=\"token operator\">&amp;</span>a<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> <span class=\"token operator\">&amp;</span>a<span class=\"token punctuation\">[</span>min_index<span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Properties</h3>\n<ul>\n<li>Space Complexity: <b>O(n)</b></li>\n<li>Time Complexity: <b>O(n<sup>2</sup>)</b></li>\n<li>Sorting in Place: <b>Yes</b></li>\n</ul>\n<h3>Visualization</h3>\n<ul>\n<li><a href=\"https://www.cs.usfca.edu/~galles/visualization/ComparisonSort.html\">USFCA</a></li>\n<li><a href=\"https://www.hackerearth.com/practice/algorithms/sorting/selection-sort/visualize/\">HackerEarth</a></li>\n</ul>\n<h3>References</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Selection_sort\">Wikipedia</a></li>\n<li><a href=\"https://www.khanacademy.org/computing/computer-science/algorithms#sorting-algorithms\">KhanAcademy</a></li>\n</ul>","fields":{"slug":"/algorithms/sorting-algorithms/selection-sort/"},"frontmatter":{"title":"Selection Sort"}}},"pathContext":{"slug":"/algorithms/sorting-algorithms/selection-sort/"}}

